import React, { Component } from "react";
import { withStyles, createStyles, Button, CircularProgress } from "@material-ui/core";
import GroupBubble from "./GroupBubble";
import Cookies from "js-cookie";
import profilePic1 from "../assets/profilePic1.jpg";
import profilePic2 from "../assets/profilePic2.jpg";
import profilePic3 from "../assets/profilePic3.jpg";
import profilePic4 from "../assets/profilePic4.jpg";
import profilePic5 from "../assets/profilePic5.jpg";
import profilePic6 from "../assets/profilePic6.jpg";
import profilePic7 from "../assets/profilePic7.jpg";
import Screen from "./Screen";
import { useEffect } from "react";
import { useState } from "react";
import { useHistory } from "react-router-dom";
import MeetableBar from "./MeetableBar";

let pics = [
  profilePic1,
  profilePic2,
  profilePic3,
  profilePic4,
  profilePic5,
  profilePic6,
  profilePic7,
];
const GroupsPage = (props) => {
  const [groups, setGroups] = useState([]);
  const history = useHistory();
  const [meetableId, setMeetableId] = useState();
  useEffect(() => {
    // if (!Cookies.get("user")) return;
    console.log(Cookies.get("meetableID"));
    const uid = Cookies.get("meetableID")
    setMeetableId(Cookies.get("meetableID"))
    const f = async () => {
      let res = await fetch(
        "http://localhost:4000/api/users/getgroupsbyuserid/" +
        uid,
        {
          method: "get",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      ).catch((e) => console.log(e));

      let groups = await res.json();
      console.log(groups);

      groups = groups.filter(Boolean)

      if (!groups.length) {
        matchUser();
      }
      setGroups(groups);
    };
    if (!meetableId) {
      console.log("no meetable id for whatever reason");
    }
    f();
  }, [meetableId]);

  const matchUser = async () => {
    console.log(meetableId);

    if (meetableId) {

      let res = await fetch("http://localhost:4000/api/users/group", {
        method: "put",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          uid: meetableId,
        }),
      });
      const g = await res.json();

      setGroups([...groups, g]);
    }
  };

  return (
    <Screen>
      <div
        style={{
          height: "100%",
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          overflow: "auto",
        }}
        className="groupsPage"
      >
        <MeetableBar showBackButton />

        {groups.length > 0 ? (
          groups.map((group, i) => {
            return (
              <GroupBubble
                index={i}
                title={`Meetable group ${i + 1}`}
                tags={group.name}
                images={[profilePic1, profilePic2, profilePic3]}
                members={group.members}
                onClick={() => {
                  history.push(`/group/${group._id}`);
                }}
              />
            );
          })
        ) : (
            <>
            <CircularProgress/>
              {/* <h3>You are not currently in a group..</h3> */}

              {/* <Button onClick={matchUser}>Match me</Button> */}
            </>
          )}
        {/* <GroupBubble
          index={1}
          color="#F5A159"
          title="Group Name 1"
          tags={["2024", "major", "interest", "interest"]}
          images={pics}
        />
        <GroupBubble
          index={2}
          color="#F5A159"
          title="Group Name 1"
          tags={["2024", "major", "interest", "interest"]}
          images={pics}
        />
        <GroupBubble
          index={3}
          color="#F5A159"
          title="Group Name 1"
          tags={["2024", "major", "interest", "interest"]}
          images={pics}
        />
        <GroupBubble
          index={4}
          color="#F5A159"
          title="Group Name 1"
          tags={["2024", "major", "interest", "interest"]}
          images={pics}
        />
        <GroupBubble
          index={5}
          color="#F5A159"
          title="Group Name 1"
          tags={["2024", "major", "interest", "interest"]}
          images={pics}
        /> */}
      </div>
    </Screen>
  );
};

const muiStyles = createStyles({});
export default withStyles(muiStyles)(GroupsPage);
