import React, { Component } from "react";
import { withStyles, createStyles, Button } from "@material-ui/core";
import Cookies from "js-cookie";
import Screen from "./Screen";
import Question1 from "./Question1";
import Question2 from "./Question2";
import Question3 from "./Question3";
import Question4 from "./Question4";
import Question5 from "./Question5";
import Question6 from "./Question6";
import Question7 from "./Question7";
import Question8 from "./Question8";
import Question9 from "./Question9";
import Question10 from "./Question10";
import MeetableBar from "./MeetableBar";
import MakeSocial from "./MakeSocial";
import { Redirect } from "react-router-dom";
import { useState } from "react";
import { useEffect } from "react";
import LoginButton from "./LoginButton";
import FlexDiv from "../design-system/FlexDiv";
import { useAuth0 } from "@auth0/auth0-react";

const Quiz = (props) => {
  const { loginWithRedirect } = useAuth0();
  const [quizInstance, setQuizInstance] = useState();
  const createQuizInstance = async () => {
    let res = await fetch("http://localhost:4000/api/quizs", {
      method: "post",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
    }).catch((e) => console.log(e));
    let json = await res?.json();
    console.log(json);
    setQuizInstance(json?.quizInstance);
  };
  useEffect(() => {
    createQuizInstance();
  }, []);
  const [form, setForm] = useState([
    {
      question: "What year are you going into?",
      possibleAnswers: ["1st Year", "2nd Year", "3rd Year", "4th Year+"],
      userAnswer: "",
    },
    {
      question: "What is your intended major?",
      possibleAnswers: [
        "Arts",
        "Sciences",
        "Computer Sciences",
        "Commerce",
        "Math",
        "Engineering",
      ],
      userAnswer: "",
    },
    {
      question:
        "Are you going to be studying on campus or off campus this term?",
      possibleAnswers: ["On Campus", "Off Campus"],
      userAnswer: "",
    },
    {
      question: "What is your favourite activity to do on a day off?",
      possibleAnswers: [
        "Go on an outdoor adventure",
        "Curl up with a good book",
        "Binge a Netflix series",
        "Spend time with my family",
        "Blast some music",
        "I don't take days off!",
      ],
      userAnswer: "",
    },
    {
      question: "Indoors or outdoors?",
      possibleAnswers: ["Indoors", "Outdoors"],
      userAnswer: "",
    },
    {
      question: "What is your favourite genre of music?",
      possibleAnswers: [
        "Pop",
        "Hip Hop",
        "Country",
        "Rock",
        "Electronic",
        "R&B",
        "Rock",
      ],
      userAnswer: "",
    },
    {
      question: "Sports and fitness are...",
      possibleAnswers: [
        "A huge part of my life",
        "Not something I'm big on",
        "It depends on the day",
      ],
      userAnswer: "",
    },
    {
      question: "What type of friend are you looking for?",
      possibleAnswers: [
        "Someone to study with",
        "Someone to go on adventures with",
        "Someone to drink with",
        "Someone to workout or play sports with",
        "I don't have a preference!",
      ],
      userAnswer: "",
    },
    {
      question: "What is your favourite cuisine?",
      possibleAnswers: [
        "American",
        "Mexican",
        "Indian",
        "Chinese",
        "Japanese",
        "Italian",
      ],
      userAnswer: "",
    },
  ]);
  const [currentIndex, setCurrentIndex] = useState(0);
  const answer = async (a, index = currentIndex) => {
    if (!quizInstance) return;
    let response = { ...form[index] };
    response.userAnswer = a;
    console.log(response);
    const f = [...form];
    console.log(index);

    f.splice(index, 1, response);
    console.log(f);

    setForm(f);
    let res = await fetch("https://localhost:4000/api/responses", {
      method: "put",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        quizid: quizInstance._id,
        question: response.question,
        answer: response.userAnswer,
      }),
    }).catch((e) => console.log(e));
    let json = await res?.json();
    console.log(json);
    console.log(res);
    next();
  };
  const next = () => {
    if(quizInstance._id) {
      Cookies.set("quizId", quizInstance._id);
      console.log("set quiz id cookie to" + quizInstance._id);
    }
    if (currentIndex < form.length) {
      setCurrentIndex(currentIndex + 1);
    }
   
  };
  return (
    <Screen>
      <MeetableBar
        showBackButton
        onBack={
          currentIndex - 1 >= 0
            ? () =>
                setCurrentIndex(currentIndex - 1 >= 0 ? currentIndex - 1 : 0)
            : null
        }
      />
      <div style={{ padding: 32 }}>
        {currentIndex === form.length && (
          <FlexDiv vert justify="center" style={{ height: "80%" }}>
            <div className="mainfont">
              <h2>You're (almost) all set!</h2>
              <p>Click to start pairing up with your classmates</p>
              <StyledButton
                classes={{ root: {} }}
                onClick={async () => {
                  loginWithRedirect({ redirectUri: "https://gomeetable.com" });
                }}
              >
                Match me
              </StyledButton>
            </div>
          </FlexDiv>
        )}
        {form && currentIndex < form.length && (
          <>
            <h1
              style={{
                fontFamily: "Roboto",
                marginTop: "3em",
              }}
            >
              {form[currentIndex].question}
            </h1>
            {form[currentIndex].possibleAnswers.map((possibleAnswer, i) => {
              return (
                <StyledButton2
                  onClick={() => {
                    answer(possibleAnswer);
                  }}
                >
                  {possibleAnswer}
                </StyledButton2>
              );
            })}
          </>
        )}
      </div>
    </Screen>
  );
};

const StyledButton = withStyles({
  root: {
    background: "linear-gradient(45deg, #45CAED 30%, #02A3F4 90%)",
    borderRadius: 3,
    border: 0,
    color: "white",
    height: 25,
    width: 300,
    padding: "0 30px",
    margin: "10px",
    boxShadow: "0 3px 5px 2px rgba(255, 105, 135, .3)",
    transition: "filter .3s",
    "&:hover": {
      filter: "brightness(85%)",
    },
  },
  label: {
    fontFamily: "Roboto",
  },
})(Button);
const StyledButton2 = withStyles({
  root: {
    background: "linear-gradient(45deg, #EA7102 30%, #FFBC01 90%)",
    borderRadius: 3,
    border: 0,
    color: "white",

    width: 300,
    padding: "0 30px",
    margin: "10px",
    boxShadow: "0 3px 5px 2px rgba(255, 105, 135, .3)",
    transition: "filter .3s",
    "&:hover": {
      filter: "brightness(85%)",
    },
  },
  label: {
    fontFamily: "Roboto",
  },
})(Button);

const muiStyles = createStyles({});
export default withStyles(muiStyles)(Quiz);
