import { useEffect } from "react";
import { useState } from "react";
import { useHistory } from "react-router-dom";
import MeetableBar from "./MeetableBar";
import Dropzone from "react-dropzone";
import noProfilePic from "../assets/noProfilePic.png";
const Bio = withStyles({
  root: {
    marginBottom: "10px",
    // backgroundColor: "#FEAC00",
    width: "100%",
    alignSelf: "center",
    fontFamily: "Roboto !important",
    borderRadius: "5px",
    "& label": {
      // color: "white",
      fontFamily: "Roboto !important",
    },
  },
})(TextField);
const SocialText = withStyles({
  root: {
    marginBottom: "10px",
    // backgroundColor: "#FEAC00",
    width: "75%",
    alignSelf: "center",
    fontFamily: "Roboto !important",
    borderRadius: "5px",
    "& label": {
      // color: "white",
      fontFamily: "Roboto !important",
    },
  },
})(TextField);

const inputProps = {
  style: {
    fontFamily: "Roboto",
  },
};

const MakeSocial = (props) => {
  const [uid, setUid] = useState();
  const [form, setForm] = useState({
    name: "",
    instagram: "",
    snapchat: "",
    facebook: "",
    bio: "",
    bioError: false,
    textError: false,
    imageError: false,
    image: [],
  });
  const history = useHistory();
  const [qid, setQid] = useState();

  const formRef = useRef();
  useEffect(() => {
    console.log(props);

    setQid(Cookies.get("quizId"));

    const u = props.meetableId ? props.meetableId : props.match.params.id;
    setUid(u);
    const f = async () => {
      let res = await fetch(`http://localhost:4000/api/users/getById/${u}`, {
        method: "get",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });

      let json = await res.json();
      let user = json.user;
      console.log(json);
      setForm({
        ...form,
        facebook: user.facebook,
        instagram: user.instagram,
        snapchat: user.snapchat,
        name: user.name,
        bio: user.blurb,
        s3URL: user.avatar,
      });
    };
    f();
  }, [props]);

  const handleImageDropZone = (files) => {
    uploadImage(files[0]).then((res) => {
      if (res) {
        if (res.status == 200) {
          const s3URL = res.body;
          console.log(s3URL);
          console.log(s3URL.location);
          setForm({ ...form, s3URL });
        }
      }
    });
  };

  const answer = async () => {
    if (!uid || !((form.facebook || form.snapchat || form.instagram) && form.name)) {
      setForm({
        ...form,
        textError: "You must include at least one social media, and your name"
      })
      return;
    };

    let res = await fetch("http://localhost:4000/api/users/updateprofile", {
      method: "put",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        uid,
        avatar: form.s3URL,
        blurb: form.bio,
        instagram: form.instagram,
        snapchat: form.snapchat,
        facebook: form.facebook,
        name: form.name
      }),
    });
    console.log(form);

    let json = await res.json();
    console.log(json);
    const user = json.user;

    //update responses with uid
    const body = JSON.stringify({
      uid,
      qid,
    });
    if (!user.groups.length) {
      console.log(body);
      let res2 = await fetch("http://localhost:4000/api/quizs/byid", {
        method: "put",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        body,
      });
      console.log(form);

      let json = await res2.json();
      console.log(json);
      const uid = props.meetableId ? props.meetableId : props.match.params.id;
    }
      // history.push(`/groups/${uid}`);

  };

  return (
    <Screen
      nicheStyle={{
        justifyContent: "center",
        alignItems: "center",
        flexDirection: "column",
        display: "flex",
        fontFamily: "Roboto",
        paddingBottom: 0,
      }}
    >
      <MeetableBar showBackButton />
      <h2
        style={{
          fontFamily: "Roboto",
          marginBottom: 0,
          "-webkit-user-select": "none",
          "-moz-user-select": "none",
          "-ms-user-select": "none",
          "user-select": "none",
        }}
      >
        Almost there!
      </h2>
      <div
        style={{
          "-webkit-user-select": "none",
          "-moz-user-select": "none",
          "-ms-user-select": "none",
          "user-select": "none",
        }}
      >
        <h3>Customize your bio</h3>
        <p>This will be visible to your friend group</p>
      </div>
      <div
        style={{
          width: "75%",
          minHeight: "500px",
          display: "flex",
          flexDirection: "column",
          borderRadius: "20px",
          background: `linear-gradient(180deg, rgba(255, 255, 255, 0.55) 0%, rgba(255, 255, 255, 0) 100%), #F5A159`,
          marginTop: "20px",
          paddingBottom: "10px",
          overflow: "hidden",
          alignSelf: "center",
        }}
        className="marginLast"
      >
        <Dropzone
          maxFiles={1}
          accept="image/*"
          onDrop={(acceptedFiles) => handleImageDropZone(acceptedFiles)}
        >
          {({ getRootProps, getInputProps }) => (
            <section>
              <div {...getRootProps()}>
                <input {...getInputProps()} />
                <div style={{ cursor: "pointer" }}>
                  <p
                    style={{
                      marginBottom: 0,
                      "-webkit-user-select": "none",
                      "-moz-user-select": "none",
                      "-ms-user-select": "none",
                      "user-select": "none",
                    }}
                  >
                    {" "}
                    Drag and drop a photo or click to upload
                  </p>
                  <img
                    {...getInputProps}
                    src={form.s3URL || noProfilePic}
                    alt="profile"
                    style={{
                      borderRadius: "20px",
                      margin: "25px",
                      marginBottom: 0,
                      height: "calc(100% - 50px)",
                      minHeight: "200px",
                      maxHeight: "300px",
                      width: "auto",
                      overflow: "hidden",
                      objectFit: "cover",
                      willChange: "transform",
                    }}
                  />
                </div>
              </div>
            </section>
          )}
        </Dropzone>
        <div
          style={{
            marginTop: "10px",
            marginBottom: "10px",
            fontFamily: "Roboto",
            fontStyle: "normal",
            fontWeight: "200",
            fontSize: "25px",
            lineHeight: "32px",
            textAlign: "center",
            // textTransform: "capitalize",
          }}
        >
          <SocialText
            error={form.textError}
            helperText={form.textError}
            inputProps={inputProps}
            label="Name"
            value={form.name}
            onChange={(e) => {
              setForm({ ...form, name: e.target.value });
              console.log(form);
            }}
          />
        </div>
        <div
          className="noScroll profile-textbox"
          style={{
            padding: "10px",
            margin: "10px",
            maxHeight: "200px",
            overflow: "auto",
          }}
        >
          <Bio
            multiline
            rows={5}
            inputProps={inputProps}
            value={form.bio}
            onChange={(e) => {
              setForm({ ...form, bio: e.target.value });
              console.log(form);
            }}
          />
        </div>
        <FlexDiv container justify="center" align="center">
          <img
            alt="snapLogo"
            style={{ height: 30 }}
            src="https://lh3.googleusercontent.com/KxeSAjPTKliCErbivNiXrd6cTwfbqUJcbSRPe_IBVK_YmwckfMRS1VIHz-5cgT09yMo"
          />
          <SocialText
          error={form.textError}
          helperText={form.textError}
            inputProps={inputProps}
            label="Snapchat"
            value={form.snapchat}
            onChange={(e) => {
              setForm({ ...form, snapchat: e.target.value });
              console.log(form);
            }}
          />
        </FlexDiv>
        <FlexDiv container justify="center" align="center">
          <img
            alt="fbLogo"
            style={{ height: 30 }}
            src="https://img.utdstc.com/icons/facebook-android.png:225"
          />
          <SocialText
          error={form.textError}
          helperText={form.textError}
            inputProps={inputProps}
            label="Facebook"
            value={form.facebook}
            onChange={(e) => {
              setForm({ ...form, facebook: e.target.value });
              console.log(form);
            }}
          />
        </FlexDiv>
        <FlexDiv container justify="center" align="center">
          <img
            alt="instaLogo"
            style={{ height: 30 }}
            src="https://upload.wikimedia.org/wikipedia/commons/thumb/e/e7/Instagram_logo_2016.svg/1024px-Instagram_logo_2016.svg.png"
          />
          <SocialText
          error={form.textError}
          helperText={form.textError}
            inputProps={inputProps}
            label="Instagram"
            value={form.instagram}
            onChange={(e) => {
              setForm({ ...form, instagram: e.target.value });
              console.log(form);
            }}
          />
        </FlexDiv>
      </div>
      <StyledButton
        onClick={async () => {
          await answer();
          console.log("hello");
          history.push("/groups");
        }}
        style={{ marginTop: 32 }}
      >
        Save and continue
      </StyledButton>
    </Screen>
  );
};

const StyledButton = withStyles({
  root: {
    background: "linear-gradient(45deg, #45CAED 30%, #02A3F4 90%)",
    borderRadius: 3,
    border: 0,
    color: "white",
    height: 25,
    width: 300,
    padding: "0 30px",
    margin: "10px",
    boxShadow: "0 3px 5px 2px rgba(255, 105, 135, .3)",
  },
  label: {
    fontFamily: "Roboto",
  },
})(Button);
const muiStyles = createStyles({});
export default withStyles(muiStyles)(MakeSocial);
