import React from 'react'
import { useHistory } from "react-router-dom";
import { withStyles, Button, ButtonBase } from "@material-ui/core";

export default function TosButton() {
    const history = useHistory();

    return (
        <ButtonBase 
            style={{marginBottom:100}}
            onClick={() => {
                history.push("/tos");
              }}
        >
            User Agreements
        </ButtonBase>
    )
}


