import React, { Component } from "react";
import meetableLogo from "../assets/meetableLogo.svg";
import { ArrowLeft } from "@material-ui/icons";
import { IconButton } from "@material-ui/core";
import { useHistory } from "react-router-dom";
const MeetableBar = (props) => {
  const history = useHistory();
  return (
    <div
      style={{
        width: "100%",
        backgroundColor: "#FADAC5",
        boxShadow: "0px 4px 4px rgba(0, 0, 0, 0.25)",
        minHeight: "70px",
        height: "10%",
        maxHeight: "100px",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",

        willChange: "transform",
      }}
    >
      {props.showBackButton && (
        <IconButton
          onClick={() => {
            if (!props.onBack) history.goBack();
            else props.onBack();
          }}
          style={{ position: "absolute", left: "20px" }}
        >
          <ArrowLeft />
        </IconButton>
      )}
      <img
        src={meetableLogo}
        alt=""
        style={{ maxHeight: "100px", minHeight: "70px", height: "20%" }}
      />
      <p
        style={{
          marginLeft: "10px",
          fontFamily: "Roboto",
          fontSize: "1.6em",
          fontWeight: "bold",
        }}
      >
        Meetable
      </p>
    </div>
  );
};

export default MeetableBar;
