import React, { Component } from "react";
import { withStyles, createStyles, Button } from "@material-ui/core";
import Cookies from "js-cookie";

class EssayForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: "",
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({ value: event.target.value });
  }

  async handleSubmit(event) {
    let res = await fetch("http://localhost:4000/api/forms/", {
      method: "post",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        uid: Cookies.get("meetableID"),
        text: this.state.value,
      }),
    }).catch((e) => console.log(e));
    console.log(await res.json());

    alert(
      "Thank you so much for taking the time to submit your feedback! It is extremely valuable to us and we really appreciate it!"
    );
    event.preventDefault();

    this.setState({ value: "" });
  }
  render() {
    return (
      <form id="create-from">
        <textarea
          style={textareaStyle}
          value={this.state.value}
          onChange={this.handleChange}
          placeholder="Please write your feedback here..."
        />
        <div>
          <StyledButton classes={{ root: {} }} onClick={this.handleSubmit}>
            Submit
          </StyledButton>
        </div>
      </form>
    );
  }
}

const textareaStyle = {
  height: 300,
  width: 350,
  background: "transparent",
  borderColor: "black",
  borderWidth: 1,
};

const StyledButton = withStyles({
  root: {
    background: "linear-gradient(45deg, #45CAED 30%, #02A3F4 90%)",
    borderRadius: 3,
    border: 0,
    color: "white",
    height: 35,
    padding: "0 30px",
    boxShadow: "0 3px 5px 2px rgba(255, 105, 135, .3)",
    transition: "filter .3s",
    "&:hover": {
      filter: "brightness(85%)",
      transition: "filter .3s",
    },
  },
  label: {
    fontFamily: "Roboto",
  },
})(Button);

const muiStyles = createStyles({});
export default withStyles(muiStyles)(EssayForm);
